:root {
    --card_width: 250px;
    --card_border_radius: 16px;
    --row_increment: 10px;
    --card_small: 26;
    --card_medium: 33;
    --card_large: 45;
    
}
body {
    margin: 0;
    padding: 0;
    background-color: lightgray;
}
.project-title {
    display: flex;
    position: relative;
    top: 50px;
    color: white;
    font-weight: 400;
    font-size: 36px;
    align-items: center;
    justify-content: center;
}
.pin-container {
    margin-top: 100px;
    padding: 0;
    width: 80vw;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);

    display: grid;
    grid-template-columns: repeat(auto-fill, 250px);
    grid-template-columns: repeat(auto-fill, var(--card_width));
    grid-auto-rows: 10px;
    grid-auto-rows: var(--row_increment);
    justify-content: center;
/* 
    background-color: black; */



}
.card {
    padding: 0px;
    margin: 15px 10px;
    background-color: transparent;
    border: 0px solid rgb(173, 173, 173);
    border-radius: 16px;
    border-radius: var(--card_border_radius);

}

.img {
    display: flex;
    flex-grow: 1;
    flex-basis: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 16px;
    border-radius: var(--card_border_radius);
    

}
.card_small {
    grid-row-end: span 26;
    grid-row-end: span var(--card_small);
}

.card_meduim {
    grid-row-end: span 33;
    grid-row-end: span var(--card_medium);
}

.card_large {
    grid-row-end: span 45;
    grid-row-end: span var(--card_large);
}
*{
    margin: 0;
    padding: 0;
}
.contacts-container {
    display: flex;
    position: absolute;
    top: 30%;
    left: 50%;
    width: 30vw;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: rgb(225, 225, 225);
    border-radius: 16px;
    height: 400px;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    
}

.form-box input {
    display: flex;
    background-color: transparent;
    border: 0.5px solid rgb(36, 27, 49);
    border-radius: 6px;
    padding: 8px;
    margin: 15px;
    width: 300px;
    font-size: 16px;
    font-weight: 400;
    outline: none;

    color: rgb(65, 65, 65);
    position: relative;
}

.form-box input::-webkit-input-placeholder { 
    color: rgb(128, 128, 128);
}

.form-box input::placeholder { 
    color: rgb(128, 128, 128);
}

.textarea-box {
    /* height: 90px;
    width: 100%; */
    display: flex;
    margin: 16px;
    background-color: transparent;
    border: 0.5px solid rgb(36, 27, 49);
    border-radius: 6px;
    padding: 8px;
    outline: none;
    font-size: 16px;
    font-weight: 400;
    color: rgb(65, 65, 65);
    resize: none;
}
.textarea-box::-webkit-input-placeholder {
    color: rgb(128, 128, 128);
}
.textarea-box::placeholder {
    color: rgb(128, 128, 128);
}
.send-email {
    display: flex;
    background-color: transparent;
    border: 0.5px solid rgb(36, 27, 49);
    border-radius: 6px;
    padding: 8px;
    width: 130px;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: rgb(42, 41, 41);;
    font-size: 16px;
    font-weight: 300;


}
.send-email:hover {
    color: white;
    font-size: 16px;
    font-weight: 300;
    cursor: pointer;
    background-color:rgba(46,48,71,1)
}
